@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200&family=Titillium+Web:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: Nunito Sans;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header,
.footer {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
/* profile and about component */
.sub-header,
.profile-header,
.Edu,
.Car,
.Ski {
  font-family: 'Syne Mono';
  color: teal;
}
.profile-header {
  padding-top: 50px;
}
div.prof,
div.carreer {
  text-align: center;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 50px;
}
p.prof {
  display: inline-block;
  width: 60%;
  text-align: center;
}
table#about,
table#educationTable,
table#carreer,
table#skills {
  padding-top: 50px;
  display: inline-block;
  width: 60%;
  table-layout: fixed;
  text-align: left;
  -webkit-justify-content: center;
          justify-content: center;
  border-collapse: separate;
  border-spacing: 0 1em;
}

img.profpic {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  border-radius: 25%;
  width: 45%;
}

td#prof-heading,
td#prof-dets {
  width: 25%;
  vertical-align: text-top;
}

/* td#prof-dets {
  width: 25%;
  vertical-align: text-top;
} */

/* Education component style */
.Education,
.Skills {
  padding-top: 50px;
  text-align: center;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 50px;
  background-color: lightgrey;
}
td#LPUh,
td#LPUd,
td#KVCHh,
td#KVCHd,
td#CCNAh,
td#CCNAd,
td#MTAh,
td#MTAd,
td#DKd,
td#DHh,
td#php,
td#react,
td#html,
td#css,
td#mysql,
td#mssql,
td#linux,
td#git {
  width: 50%;
  vertical-align: text-top;
}

